export class CreatCustExplCondBean {
    expCondId:string;//探索条件编号
    screenTemplateName: string;//探索条件名称
    sharedScope:string;//共享范围
    status:string;//模板状态
    sysModelId: string;//系统模型
    sysModelName: string;//系统模型名称
    modelScale: string;//模型客户比例
    statusId: string;//状态标志
    // qryRange: QryRange = new QryRange();//1.查询范围
    custInfo: CustInfo = new CustInfo();//客户基本信息
    indiCustomer: IndiCustomer = new IndiCustomer();//个人客户属性
    businessInfo: BusinessInfo = new BusinessInfo();//经营信息
    custHoldProdLists: string;//7.客户持有产品标识
    custHoldProdAttrList: any;//8.客户产品属性列表
    custLabelAttrList: any;//9.客户标签属性列表
    custGroupAttrList: any;//10.客户群属性列表
    ftpAttrList: any;//11.FTP利润属性列表
};



//4. 客户基本信息
export class CustInfo {
    custType: string;                    //客户类型
    custNo: string;                       //客户号  
    custName: string;                    //客户名称 
    custAttr: string;                      //客户属性
    isAgriculture: string;             //涉农属性  
    cycle: string;                     //客户生命周期 
    crdRateType: string;               //信用评分类型
    crdRateStart: string;             //信用评级起
    crdRateEnd: string;             //信用评级止  
    afterLoanRateType: string;     //贷后评分类型    
    creatTimeStart: string;       //开户日期起   
    creatTimeEnd: string;        //开户日期止   
    isCancel: string;           //是否注销    
    IDCardType: string;        //证件类型    
    IDCardNo: string;         //证件号码   
    tel: string;             //联系号码
    idCardType;
    idCardNo;
}

//5.个人客户属性
export class IndiCustomer {
    sexs: string;//性别
    residType: string;  //户口性质  
    maritalStatus: string;//婚姻状态
    manageAddr: string;      //经营地址
    familyAddr: string;//家庭地址 
    monthIncomeStart: string;//月收入起
    monthIncomeEnd: string; // 月收入止
    yearIncomeStart: string;//年收入起
    yearIncomeEnd: string;//年收入止
    birthDateStart: string;//出生日期起
    birthDateEnd: string; //出生日期止
    ageFirstFrom: string;//年龄区间起1
    ageFirstTo: string;//年龄区间止1
    ageSecondFrom: string;//年龄区间起2
    ageSecondTo: string;//年龄区间止2 
}
//5.经营信息
export class BusinessInfo {
    industry: string;//所属行业 
    entType: string;// 中小企业类型  
    corpName: string;//法人代表    
    mobile: string;//法人代表联系号码   
    assetUp: string;//资产总额(上限） 
    assetDown: string;//资产总额(下限）   
    debtUp: string;//负债总额(上限） 
    debtDown: string;//负债总额(下限）   
    busAddrCode: string;//经营地址                         
}



// 8.客户产品属性筛选
export class custHoldProdAttrList {
    custHoldProdAttrListMember: Array<custHoldProdAttrListMember>;//客户产品属性列表成员
}
export class custHoldProdAttrListMember {
    prodCateId: string;//产品大类标识
    subProdCateId: string;// 产品小类标识
    paramTypeId: string;//参数类型标识
    prodParmFrom: string;//产品参数值（上限）
    prodParmTo: string;//产品参数值（下限）
}

// 9.客户标签属性筛选
export class custLabelAttrList {
    custLableAttrListMember: Array<custLableAttrListMember>//客户标签属性列表成员
}
export class custLableAttrListMember {
    labelTypeId: string;//标签类型编号
    labelId: string;//标签编号
    labelName:string;//标签名称
}

//10.客户群属性筛选
export class custGroupAttrList {
    custGroupAttrListMember: Array<custGroupAttrListMember>//客户群属性列表成员
}
export class custGroupAttrListMember {
    custGrouTypeId: string;//客户群类型编号
    custGrouId: string;//客户群编号
    custGrouName: string;//客户群名称
}


//11.FTP利润属性筛选
export class ftpAttrList {
    ftpAttrListMember: Array<ftpAttrListMember>//FTP利润属性列表成员
}
export class ftpAttrListMember {
    profit: string;// FTP利润时间
    profitAttr: string;// FTP利润属性
    profitParmFrom: string;//FTP利润参数值（上限）
    profitParmTo: string;//FTP利润参数值（下限）     
}



//打开探索条件模态框的bean
export class CustExplBean {
    expCondName: string;//探索名称
    creatTimeStart: string;//创建时间起
    creatTimeEnd: string;//创建时间止
    pageNum: number;//页数
    pageSize: number;//每页显示条数
}

//选择模板模态框bean
 
export class CusTemplateBean{
    screenTemplateName:string;//筛选模板名称
    marketingTimeFrom:string;//创建时间起
    marketingTimeTo:string;//创建时间止
    createOrgId:string;//创建机构
    createUserId:string;//创建人
    sharedScope:string;//共享范围
    status:string;//模板状态
    pageNum:number;//页数
    pageSize:number;//每页显示条数
}