import { Component, OnInit } from '@angular/core';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service'
import { FilterCustomBean } from './bean/addSearchAmanage.bean';
import { Router } from '@angular/router';
import { Message, ConfirmationService, DialogModule } from 'primeng/primeng';
import { ShareRangeOpt, TempStatusOpt } from './constant/addSearchAmanage.codeValue';
import * as _ from 'lodash';

//筛选客户模板管理
@Component({
  selector: 'app-add-search-manage',
  templateUrl: './add-search-manage.component.html',
  styleUrls: ['./add-search-manage.component.scss'],
  providers: [CusOperationHttpService, Commfunc, ConfirmationService]
})

//筛选客户模板管理
export class AddSearchManageComponent implements OnInit {
  // 表格中显示的信息
  tabMesg: any = tableMessage;
  role: string;
  userId: string; // 用户id
  //日期
  ch: any = CALENDAR_CN;
  creatStartTime: Date = new Date();//创建时间开始
  creatEndTime: Date = new Date();//创建时间开始
  //表数据
  tableData: any = [];
  // 目标客户探索条件表格数据
  custExplTable: any = [];
  //bean
  filterCustomBean: FilterCustomBean = new FilterCustomBean();
  total: number;//总条数
  //提示信息
  msgs: Message[] = [];
  //下拉框
  shareRangeOpt: any[] = ShareRangeOpt;//共享范围
  TempStatusOpt: any[] = TempStatusOpt//模板状态
  createOrgId: string = '';//创建机构绑定的值
  creatOrgOpt: any = [{ orgName: "请选择", orgId: "" }];//创建机构
  orgOption: any = [];
  creatOrgOpts: any = [];//创建机构
  creatPeopleOpts: any = [];//创建人
  treeOrgName: any;//机构号
  createUserName: any;//机构名称
  orgTreeDisplay: boolean = false;
  orgTreeDisplay2: boolean = false;
  queryUserInValue: boolean = false;
  userName: string; // 登陆用户名
  location: any = [];//位置描述
  showOrgDis: boolean = false;//当共享范围为私有时，创建机构和创建人都只可读
  marketReleaseDisplay: boolean = false; // 发布确定 模板
  templateDisplay: boolean = true;
  showFlag: Boolean = true;
  userNames:string;
  constructor(
    public router: Router,
    public httpService: CusOperationHttpService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc
  ) {
    this.creatStartTime = null;
    this.creatEndTime = null;

  }

  ngOnInit() {
    this.filterCustomBean.pageSize = 10;
    this.filterCustomBean.pageNum = 1;
    this.userNames = JSON.parse(this.commfunc.getSessionDataCH('chName')).userName;

    this.getSession();
  }



  // 浏览器滚动条状态 - 滚动
  onHideClose() {
    document.body.style.overflow = 'auto';
  }
  // 主联系人机构
  openOrgTreeClick() {
    this.orgTreeDisplay = !this.orgTreeDisplay;
    document.body.style.overflow = 'hidden';
  }
  // 主联系人
  openOrgTreeClick1() {
    this.orgTreeDisplay2 = !this.orgTreeDisplay2;
    document.body.style.overflow = 'hidden';
  }
  // 机构树回调
  treeCall(param) {
    let orgId: any, orgIds = '';
    this.orgTreeDisplay = false;
    this.treeOrgName = []
    // param.forEach((v) => {
    this.treeOrgName.push(param.orgName)
    // orgId.push(param.orgId)
    // this.filterCustomBean.createOrgId = orgId;
    this.filterCustomBean.createOrgId = param.orgId;
    this.queryUserInValue = param.orgId;
    document.body.style.overflow = 'auto';
  }
  perInfor(param) {
    let orgId = [], orgIds = '';
    this.orgTreeDisplay2 = false;
    this.createUserName = []
    param.forEach((v) => {
      this.createUserName.push(v.tellerName)
      orgId.push(v.tellerId)
    })
    this.filterCustomBean.createUserId = orgId;
    document.body.style.overflow = 'auto';
  }



  //点击查询按钮 ，将选中的创建机构id传给后台
  queryClick() {
    this.filterCustomBean.creatTimeStart = this.commfunc.transDateN(this.creatStartTime);
    this.filterCustomBean.creatTimeEnd = this.commfunc.transDateN(this.creatEndTime);
    // this.filterCustomBean.createOrgId = this.createOrgId.split(',');//创建机构，将字符串转换成数组（接口中的创建机构格式是数组）
    this.query();
  }


  //查询列表
  query() {
    this.filterCustomBean.requestType = "1";
    this.filterCustomBean.queryScene = "1";
    this.httpService.queryTargetCustomerExploreList(this.filterCustomBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.custExplTable = data.screenTemplateList;
        // this.custExplTable.forEach((v,i) => {
        //   if (v.createUserName == this.userName) {
        //     this.templateDisplay = true;
        //   }
        // })
        this.total = data.totalCount;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '查询列表提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '查询列表提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '查询列表提示', detail: '调用服务失败！' });
    })
  }

  checkRadioValue(event, i) {
  }


  // 获取session值 初始化必要参数
  getSession() {

    this.userId = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;
    let param = JSON.parse(sessionStorage.chName)
    this.userName = param.userName
  }

  // 列表的发布按钮
  marketRelease(item) {
    if (item.createUserId == this.userId) {

      if (item.status == '4') {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '发布失败', detail: '不能发布已共享模板！' });
        return;
      }
      this.filterCustomBean.screenTemplateId = item.screenTemplateId;
      this.filterCustomBean.screenTemplateName = item.screenTemplateName;
      // this.marketReleaseDisplay = !this.marketReleaseDisplay;
      this.confirmationService.confirm({
        message: '是否确认发布?',
        header: '发布',
        icon: 'fa fa-question-circle',
        accept: () => {
          let param = { expCondId: this.filterCustomBean.screenTemplateId, expCondName: this.filterCustomBean.screenTemplateName };
          this.httpService.createScreenCustomerTemplate(param).subscribe((data) => {
            if (data.returnCode.code === "success") {
              this.msgs = [];
              this.msgs.push({ severity: 'success', summary: '查询列表提示', detail: data.returnCode.message });
              this.query();
            } else {
              this.msgs = [];
              this.msgs.push({ severity: 'error', summary: '查询列表提示', detail: data.returnCode.message });
            }
          }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '查询列表提示', detail: '调用服务失败！' });
          });
        }
      });
    }
    else {

      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '发布失败', detail: '不能发布他人模板' });
    }
  }

  // 列表中的详情按钮，将筛选模板ID带到详情页面----(filter-custom-detail.component.ts)
  showDetail(item) {
    this.router.navigate(['/pages/tzb/custom/custom-information/advanced-search/add-search', { screenTemplateId: item.screenTemplateId }]);
  }
  Detail(item) {
    this.router.navigate(['/pages/tzb/custom/custom-information/advanced-search/add-search', { screenTemplateId: item.screenTemplateId,detail:'detail' }]);
  }

  // 列表中的删除按钮
  deleteTarget(e) {

    if (e.createUserId == this.userId) {
      this.confirmationService.confirm({
        message: '是否确认删除?',
        header: '删除',
        icon: 'fa fa-question-circle',
        accept: () => {
          let param = { expCondId: e.screenTemplateId, requestType: '1' }
          this.httpService.deleteTargetCustomerExplore(param).subscribe((data) => {
            if (data.returnCode.code === "success") {
              this.msgs = [];
              this.msgs.push({ severity: 'success', summary: '查询列表提示', detail: data.returnCode.message });
              this.query();
            } else {
              this.msgs = [];
              this.msgs.push({ severity: 'error', summary: '查询列表提示', detail: data.returnCode.message });
            }
          }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '查询列表提示', detail: '调用服务失败！' });
          });
        }
      });
    }
    else {

      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '删除失败', detail: '不能删除他人模板' });
    }
  }

  //分页
  paginate(event) {
    //每页显示的条数
    this.filterCustomBean.pageSize = event.rows * 1;
    //当前页
    let currentPage = event.page + 1;
    this.filterCustomBean.pageNum = currentPage;
    //调用查询的方法
    // this.filterCustomBean.pageSize = 10;
    // this.filterCustomBean.pageNum = 1;
    this.query();
    this.filterCustomBean.pageNum = 1;
  }

  //重置按钮
  resetClick() {
    this.filterCustomBean = new FilterCustomBean();
    this.creatStartTime = null;
    this.creatEndTime = null;
    this.treeOrgName = null;
    this.createUserName = null;
    this.createOrgId = '';
  }

  // 控制日历刷新
  refresh() {
    this.creatEndTime = null;
    this.showFlag = !this.showFlag;
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }
  faCheck() { }
  faClose() { }

  //开始日期
  showDataStart() {
    if (this.creatStartTime && this.creatEndTime) {
      if (this.creatStartTime > this.creatEndTime) {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
        setTimeout(() => {
          this.creatStartTime = null;
        })
      }
    }
  }
  //结束日期
  showDataEnd() {
    if (this.creatStartTime && this.creatEndTime) {
      if (this.creatStartTime > this.creatEndTime) {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
        setTimeout(() => {
          this.creatEndTime = null;
        })
      }
    }
  }
  //手动输入日期时，失去焦点时进行判断
  onchange1() {
    this.showDataStart()
  }
  onchange2() {
    this.showDataEnd()
  }
}