import { Component, OnInit } from '@angular/core';
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';
import { MenuItem } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Message, ConfirmationService } from 'primeng/primeng';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Routes,Router } from '@angular/router';
import { ObjectCustBean } from "app/pages/tzb/custom/custom-information/advanced-search/search-conditions-manage/bean/object-custom.bean";
import { CusExplStatus } from "app/pages/tzb/custom/custom-information/advanced-search/add-search/constant/target-custom.codeValue";

@Component({
  selector: 'app-search-conditions-manage',
  templateUrl: './search-conditions-manage.component.html',
  styleUrls: ['./search-conditions-manage.component.scss'],
  providers: [Commfunc, ConfirmationService, CusOperationHttpService]
})
export class SearchConditionsManageComponent implements OnInit {

   //日期
   public ch: any = CALENDAR_CN;
   public creatTimeStart: any;//开始日期
   public creatTimeEnd: any;//结束日期
   //表数据
   public tableData: any = [];
   //总条数
   public total: number;
   public msgs: Message[] = [];  //提示信息
   public objectCustBean: ObjectCustBean = new ObjectCustBean();
   public cusExplStatus:any=CusExplStatus;//模板状态
   totalNum;
   constructor(
     public httpService: CusOperationHttpService,
     public confirmationService: ConfirmationService,
     public commfunc: Commfunc,
     public router:Router
   ) {
 
   }
 
   ngOnInit() {
     this.tableData = [
       {custExplId:"1"},
       {custExplId:"2"}
     ]
     this.objectCustBean.pageNum = 1;
     this.objectCustBean.pageSize = 10;
    //  this.queryClick();
   }
 
   //查询按钮
   queryClick() {
     this.objectCustBean.creatTimeStart = this.commfunc.transDateN(this.creatTimeStart);
     this.objectCustBean.creatTimeEnd = this.commfunc.transDateN(this.creatTimeEnd);
     this.httpService.queryMyCustAdvanceSeachList(this.objectCustBean).subscribe(data => {
       if (data.returnCode.code == 'success') {
         this.tableData = data.custExplList;
         this.total = data.totalCount;
         this.msgs = [];
         this.msgs.push({ severity: 'success', summary: '探索条件结果', detail: data.returnCode.message });
       } else {
         this.msgs = [];
         this.msgs.push({ severity: 'error', summary: '探索条件结果', detail: data.returnCode.message });
       }
     }, error => {
       this.msgs = [];
       this.msgs.push({ severity: 'error', summary: '探索条件结果', detail: '调用服务失败！' });
     })
   }
 
   //分页
   paginateExplo(event) {
     //每页显示的条数
     this.objectCustBean.pageSize = event.rows * 1;
     //当前页
     let currentPage = event.page + 1;
     this.objectCustBean.pageNum = currentPage;
     //调用查询的方法
     this.queryClick();
     this.objectCustBean.pageNum = 1;
   }
 
   //重置按钮
   resetClick() {
     this.objectCustBean = new ObjectCustBean();
     this.creatTimeStart = null;
     this.creatTimeEnd = null;
   }
   //发布为查询模板按钮
   pushTemplClick(item){
     this.router.navigate(['/pages/tzb/custom/custom-information/advanced-search/publish-template',{custExplId:item.custExplId}]);
   }
   //打开探索条件按钮
   openExploClick(item){
      this.router.navigate(["/pages/tzb/custom/custom-information/advanced-search",{custExplId:item.custExplId}]);
   }
 
   //删除按钮事件
   deleteClick(item) {
     this.confirmationService.confirm({
       message: '确认要删除吗?',
       accept: () => {
         //条件
         let param = { expCondId: item.custExplId };
         this.httpService.deleteTargetCustomerExplore(param).subscribe(data => {
           if (data.returnCode.code=='success') {
             this.objectCustBean.pageNum = 1;
             this.queryClick();
             this.msgs=[];
             this.msgs.push({severity:'success',summary:'提示',detail: data.returnCode.message });
           } else {
             this.msgs = [];
             this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
           }
         }, error => {
           this.msgs = [];
           this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
         })
       }
     });
   }
}
