	/**----------------------------------------投向行业--------------------------------------------- */
	taskTypeQry(params) {
		this.turnIndustry = [{ 'label': '请选择', 'value': '' }];
		this.httpService.taskTypeQry(params).subscribe((data) => {
			if (data.returnCode.code === 'success') {
				data.taskTypeList.forEach((v) => {
					this.turnIndustry.push({ "label": v.taskTypeName, "value": v.taskTypeId })
				})
			}
		})
	}

	taskTypeQryChange(e) {
		this.params.taskTypeParentId = e.value;
		this.turnIndustry1 = e.value;
		this.turnIndustryOne = [{ 'label': '请选择', 'value': '' }];
		this.httpService.taskTypeQry(this.params).subscribe((data) => {
			if (data.returnCode.code === 'success') {
				data.taskTypeList.forEach((v) => {
					this.turnIndustryOne.push({ "label": v.taskTypeName, "value": v.taskTypeId })
				})
				if (e.value !== '') {
					this.indutryOneDis = true;
					this.indutryTwoDis = false;
					this.indutryThreeDis = false;
				} else {
					this.indutryOneDis = false;
					this.indutryTwoDis = false;
					this.indutryThreeDis = false;
					this.turnIndustry2 = '';
					this.turnIndustry3 = '';
					this.turnIndustry4 = '';
				}
			}
		})

	}
