	outValue5(e) {
		let prodParam0: any = '', prodParam1: any = '', prodParam2: any = '';
		this.creatCustExplCondBean.requestMap.custHoldProdAttrList = [];
		this.infoList1 = [];
		// 传到子组件 使用
		this.filterTemplate = [];
		this.displayTarget.map((v, i) => {
			this.displayTarget[i].subList = [];
		})
		this.productmodel = false;
		// 产品属性 表格展示回显
		this.productMetric1 = e;
		if (e !== undefined) {
			this.prodtotalNum = e.length;
			e.map((v, i) => {
				if (e.length > 0) {
					this.infoList.map((v1) => {
						if (v.value[0].productId == v1.productId) {
							this.infoList1.push(v1)
						}
					})
				}
				// 传到子组件 使用
				this.filterTemplate.push({ value: v.value[0].categoryName, label: v.label })
				/**--------------------------------------------------------------------------------------- */
				// 判断参数类型 显示
				let type = v.parameter[0] - 0
				if (v.parameter[0] == 'true' || v.parameter[0] == 'false') {
					if (v.parameter[0] === 'true') {
						prodParam2 = '是';
						prodParam1 = '';
					} else {
						prodParam2 = '否';
						prodParam1 = '';
					}
					prodParam0 = v.parameter[0]
					this.productMetric1[i].canshu = prodParam2;
				} else if (v.parameter[0] == '') {
					this.productMetric1[i].canshu = `${'未选择参数'}`
				} else if (typeof v.parameter[0] == 'object') {
					prodParam0 = this.commfunc.transDateN(v.parameter[0]);
					prodParam1 = this.commfunc.transDateN(v.parameter[1]);
					this.productMetric1[i].canshu = `${prodParam0 + '  至  ' + prodParam1}`
				} else {
					prodParam0 = v.parameter[0];
					prodParam1 = v.parameter[1];
					this.productMetric1[i].canshu = `${prodParam0 + '至' + prodParam1}`
				}
				if (prodParam0 == 'true' || prodParam0 == 'false') {
					this.creatCustExplCondBean.requestMap.custHoldProdAttrList.push({
						prodCateId: v.label,//产品大类标识
						subProdCateId: v.value[0].productId,// 产品小类标识
						paramTypeId: v.target,//参数类型标识
						prodParmFrom: prodParam0 //产品参数值（上限）
						// prodParmTo: prodParam0//产品参数值（下限）
					})
				} else {
					this.creatCustExplCondBean.requestMap.custHoldProdAttrList.push({
						prodCateId: v.label,//产品大类标识
						subProdCateId: v.value[0].productId,// 产品小类标识
						paramTypeId: v.target,//参数类型标识
						prodParmFrom: prodParam0 + '|' + prodParam1,//产品参数值（上限）
						// prodParmTo: prodParam0//产品参数值（下限）
					})
				}
			})
			this.chooseSelect();
			this.productMetric = this.productMetric1.slice(0, 10);
		}
		this.productMetricMethod();
		this.productTable();
	}

	productMetricMethod() {
		if (this.productMetric1) {
			this.productMetric1.map(v => {
				this.displayTarget.forEach((v1, i1) => {
					if (v.parent_node[0] !== undefined && (v.parent_node[0].label == v1.fieldTitle || (v.parent_node[0].parent !== undefined && v.parent_node[0].parent.label == v1.fieldTitle))) {
						this.displayTarget[i1].subList.push({ fieldTitle: v.label, fieldName: v.value[0].productId })
					}
				})
			})
		}


	}


	/**
	 * 高级搜索：下载客户名单用
	 * 		no1.和displayTarget多添加了一个subList3，并且在原数组中subList的fiedName存入的是产品的生产id，并不是产品的字段名
	 * 	
	 *		no2.代码中有很多层的循环，整体影响性能，需优化 
	 */
	displayTarget2() {
		let dis2: any = _.cloneDeep(this.displayTarget);
		if (this.productMetric1 && this.productMetric1.length > 0) {
			// 1.更换产品的fiedName为产品的字段名
			this.infoList1.forEach((v, i) => {
				dis2.forEach((v1, i1) => {
					if (v1.subList.length > 0) {
						dis2[i1].subList.forEach(v2 => {
							if (v.productId == v2.fieldName) {
								v2.fieldId = v2.fieldName;
								v2.fieldName = v.value;
							}
						})
					}
				})
			});
			// 2.判断是否存在subList？ 存入sbuList3：不做任何处理
			this.infoList1.forEach(v => {
				dis2.forEach((v1, i1) => {
					if (v1.subList.length > 0) {
						// 直接将数组赋予一个变量时，改变变量里面的内容同时会改变原数组。因此添加了slice()/concat()方法,可以解决此问题.
						dis2[i1].subList3 = _.cloneDeep(v1.subList);
					}
				})
			})
			// 3.查看结果的展示指标的isCheck = true？ 将对应产品的展示指标存入subList3对应的detail中： 不存，
			let product_Id: string;
			dis2.forEach((v, i) => {
				if (v.subList3 && v.subList3.length > 0) {
					v.subList3.forEach((data, index) => {
						data.detail = [];
					})
					this.custExplTable.forEach((v1, i1) => {
						// 判断产品的类型(存款类 || 贷款类)，并且 产品的展示指标是否被勾选(isCheck)
						if ((v1.property == 'saveAttr' || v1.property == 'loansAttr') && v1.isCheck) {
							v.subList3.forEach((v3, i3) => {
								this.infoList1.forEach((v2, i2) => {
									// 找到存入subList3中的产品和infoList的名称做判断？ 将当前产品的id暂存： 不存
									if (v3.fieldName == v2.value || product_Id) {
										product_Id = v2.productId
										// 如果 产品的id相同 && 和 custExplTable的展示指标的 分类相同(例如：isDisplay == 1，catelgory == 1)？
										//	将infoList中对应的产品展示指标存入到 subList3['对应的产品类'].detail 中
										if (product_Id !== '' && product_Id == v.subList3[i3].fieldId && v1.isDisplay == v2.catelgory) {
											v3.detail.push({ fieldTitle: v2.label, fieldName: v2.value })
										}
									}
								})
								product_Id = '';
							})
						}
					})
				}
			})
			// 4.对象去重
			dis2.forEach(v => {
				if (v.subList3 && v.subList3.length > 0) {
					v.subList3.forEach((data, index) => {
						let sub_arr = this.removeDulication(data.detail, 'fieldTitle', 'fieldName');
						data.detail = sub_arr;
					})
				}
			})

			this.disPlayT2 = dis2;
		}
	}

	// 对象去重（可根据属性去重）
	removeDulication(param, p_na1, p_na2) {
		let result = [], hash = {}, _hash = {};
		for (let i = 0; i < param.length; i++) {
			const ele1 = param[i][p_na1];
			const ele2 = param[i][p_na2];
			if (!hash[ele1]) {
				if (!_hash[ele2]) {
					result.push(param[i]);
					_hash[ele2] = true;
				}
				hash[ele1] = true;
			}
		}
		return result;
	}
