	//客户标签名称（模糊查询）
	cusTagName(i, ind, event) {
		this.queryID = event.query;
		if (event.query) {
			if (this.tagType == '' || !this.tagType) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '客户标签模糊查询', detail: '标签类型为必填项' });
				return;
			} else {
				//根据自己联想的标签名，进行模糊查询
				let param = { status: "1", labelName: event.query, labelType: this.tagType }
				this.selectLabelInfo(i, ind, param);

			}
		} else {
			if (this.tagType == '' || !this.tagType) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '客户标签模糊查询', detail: '标签类型为必填项' });
				return;
			} else {
				//根据回显详情后的标签名称（labelName），进行模糊查询
				let param = { status: "1", labelName: event.query, labelType: this.tagType }
				this.selectLabelInfo(i, ind, param);
			}
		}
		// if (event.query) {
		// 	//根据自己联想的标签名，进行模糊查询
		// 	let param = { status: "1", labelName: event.query,labelType:this.tagType}
		// 	this.selectLabelInfo(i, ind, param);
		// } else {
		// 	//根据回显详情后的标签名称（labelName），进行模糊查询
		// 	let param = { status: "1", labelName: event,labelType:this.tagType}
		// 	this.selectLabelInfo(i, ind, param);
		// }
	}

	//调后台客户标签模糊查询接口
	selectLabelInfo(i, ind, param) {
		this.httpService.selectLabelInfo(param).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.custLabelAttrList[i][ind]['cusTagNameOpt'] = [[]];
				this.custLabelAttrList[i][ind]['cusTagNameOpt'] = data.labelTypeList;
				let arr = data.labelTypeList;

				let flag = false;
				for (let j of arr) {
					if (j.labelName == this.queryID) {
						this.custLabelAttrList[i][ind]['labelId'] = j.labelNum;
						this.custLabelAttrList[i][ind]['labelName'] = j.labelName;
						flag = true;
					}

				}
				if (!flag) {
					this.custLabelAttrList[i][ind]['labelId'] = '';
					this.custLabelAttrList[i][ind]['labelName'] = '';

				}
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '客户标签模糊查询', detail: data.returnCode.message });
			} else {
				this.msgs = [];
				// this.msgs.push({ severity: 'error', summary: '客户标签模糊查询', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '客户标签模糊查询', detail: '调用服务失败！' });
		})
	}
	//客户标签模糊查询赋值，转化成后台想要的格式传给后台
	selectCusTagNameOpt(i, ind, event) {
		this.custLabelAttrList[i][ind]['labelId'] = event.labelNum;
		this.custLabelAttrList[i][ind]['labelName'] = event.labelName;
	}


	// 客户群属性（模糊查询）
	cusGroupName(i, ind, event) {
		debugger
		let param;
		if (this.custGroupAttrList[i][ind].custGrouTypeId == '0') {
			if (event.query) {
				param = { groupName: event.query, pageNum: 1, pageSize: 999999 };
			} else {
				param = { groupName: event, pageNum: 1, pageSize: 999999 };
			}
			this.selectCustGroup(i, ind, param);
		} else if (this.custGroupAttrList[i][ind].custGrouTypeId == '1') {
			if (event.query) {
				param = { freeGroupName: event.query, pageNum: 1, pageSize: 999999 };
			} else {
				param = { freeGroupName: event, pageNum: 1, pageSize: 999999 };
			}
			this.selectFreeGroup(i, ind, param);
		}
	}
	selectCustGroup(i, ind, param) {
		this.httpService.selectCustGroup(param).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.custGroupAttrList[i][ind]['cusGroupNameOpt'] = [[]];
				this.custGroupAttrList[i][ind]['cusGroupNameOpt'] = data.groupList;
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '客户群模糊查询', detail: data.returnCode.message });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '客户群模糊查询', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			// this.msgs.push({ severity: 'error', summary: '客户群模糊查询', detail: '调用服务失败！' });
		})
	}
	selectFreeGroup(i, ind, param) {
		this.httpService.selectFreeGroup(param).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.custGroupAttrList[i][ind]['cusGroupNameOpt'] = [[]];
				data.freeGroupList.forEach((element, i) => {
					data.freeGroupList[i].groupName = element.freeGroupName;
					data.freeGroupList[i].groupId = element.freeGroupId;
					delete data.freeGroupList[i]['freeGroupName'];
					delete data.freeGroupList[i]['freeGroupId'];
				});
				console.log(data.freeGroupList)
				this.custGroupAttrList[i][ind]['cusGroupNameOpt'] = data.freeGroupList;

				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '自选群模糊查询', detail: data.returnCode.message });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '自选群模糊查询', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '自选群模糊查询', detail: '调用服务失败！' });
		})
	}

