	/*  各种类型的码值转换                      开始
	**
	**	客户类型 CUST_TYPE、 客户属性 CUST_ATTR、涉农属性 IS_AGRICULTURE、证件类型 IDCARDTYPE、性别SEXS、
	**	户口性质 RESIDTYPE、婚姻状况 MARITALSTATUS、所属行业 INDUSTRY、中小型企业类型 ENT_TYPE
	**
	*/
	codeValueChange(i) {
		// 客户类型
		if (this.custExplTable1[i].CUST_TYPE == 2) {
			this.custExplTable1[i].CUST_TYPE = '企业客户';
		} else if (this.custExplTable1[i].CUST_TYPE == 1) {
			this.custExplTable1[i].CUST_TYPE = '个人客户';
		}
		// 客户属性
		if (this.custAttrOpt && this.custAttrOpt.length > 0) {
			this.custAttrOpt.map(v => {
				if (this.custExplTable1[i].CUST_ATTR == v.value) {
					this.custExplTable1[i].CUST_ATTR = v.label;
					return;
				}
			})
		}
		// 涉农属性
		if (this.agricuOpt && this.agricuOpt.length > 0) {
			this.agricuOpt.map(v => {
				if (this.custExplTable1[i].IS_AGRICULTURE == v.value) {
					this.custExplTable1[i].IS_AGRICULTURE = v.label;
					return;
				}
			})
		}
		// 证件类型
		if (this.iDCardOpt3 && this.iDCardOpt3.length > 0) {
			this.iDCardOpt3.map(v => {
				if (this.custExplTable1[i].IDCARDTYPE == v.value) {
					this.custExplTable1[i].IDCARDTYPE = v.label;
					return;
				}
			})
		}
		// 性别
		if (this.sexOpt && this.sexOpt.length > 0) {
			this.sexOpt.map(v => {
				if (this.custExplTable1[i].SEXS == v.value) {
					this.custExplTable1[i].SEXS = v.label;
					return;
				}
			})
		}
		// 户口性质
		if (this.houseRegOpt && this.houseRegOpt.length > 0) {
			this.houseRegOpt.map(v => {
				if (this.custExplTable1[i].RESIDTYPE == v.value) {
					this.custExplTable1[i].RESIDTYPE = v.label;
					return;
				}
			})
		}
		// 婚姻状况
		if (this.marryOpt && this.marryOpt.length > 0) {
			this.marryOpt.map(v => {
				if (this.custExplTable1[i].MARITALSTATUS == v.value) {
					this.custExplTable1[i].MARITALSTATUS = v.label;
					return;
				}
			})
		}
		// 所属行业
		if (this.professOpt && this.professOpt.length > 0) {
			this.professOpt.map(v => {
				if (this.custExplTable1[i].INDUSTRY == v.value) {
					this.custExplTable1[i].INDUSTRY = v.label;
					return;
				}
			})
		}
		// 中小型企业类型
		if (this.enterTypeOPt && this.enterTypeOPt.length > 0) {
			this.enterTypeOPt.map(v => {
				if (this.custExplTable1[i].ENT_TYPE == v.value) {
					this.custExplTable1[i].ENT_TYPE = v.label;
					return;
				}
			})
		}
	}
