    requestType: string;//服务请求类型
    expCondName:string;//筛选模板名称
    queryScene: string;//模板查询类型
    creatTimeStart:string;//创建时间起
    creatTimeEnd:string;//创建时间止
    createOrgId:string;//创建机构
    createUserId:any;//创建人
