
import { Message, ConfirmationService, SelectItem } from 'primeng/primeng';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { CALENDAR_CN } from "app/@uisftech/common/constant/config";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import { Component, OnInit } from '@angular/core';
import { MenuItem } from 'primeng/primeng';
import * as moment from 'moment';
import * as _ from 'lodash';
import { CreatCustExplCondBean, CustInfo, IndiCustomer, BusinessInfo, CustExplBean, CusTemplateBean } from './bean/publish-template.bean';
import {
    TempSharedRanOpt, IDCardOpt, CusCycleOpt, AgricuOpt, CusProAttrOpt,
    ParamOpt, SaveTypeOpt, LoanTypeOpt, CusTagOpt, CusGroupAttrOpt,
    FTPTimeOpt, FTPAtrrOpt, SexOpt, MarryOpt, HouseRegOpt, CusHoldProdOpt,
    CustAttrOpt1, CustAttrOpt2, CreditOpt, CreditStaOpt1, CreditEndOpt1,
    CreditStaOpt3, CreditEndOpt3, PublishTempShareRanOpt, ProfessOpt, EnterTypeOPt
} from '../../add-search/constant/target-custom.codeValue';

@Component({
  selector: 'app-publish-template',
  templateUrl: './publish-template.component.html',
  styleUrls: ['./publish-template.component.scss'],
  providers: [CusOperationHttpService, ConfirmationService, Commfunc]
})
export class PublishTemplateComponent implements OnInit {

   //公共的
   public creatCustExplCondBean: CreatCustExplCondBean = new CreatCustExplCondBean();
   public ch: any = CALENDAR_CN;//日期
   public msgs: Message[] = []; //提示信息
   public trueOrFalse: boolean = false;//折叠面板收起、展开
   public custExplId: string = '';//接受目标客户探索管理页面传过来的探索ID
   public exploreConditionTempId: string = '';//调保存、确定并提交后返回的探索ID

   // 共享范围
   public publishTempShareRanOpt: any[] = PublishTempShareRanOpt;

   //4.客户基本属性筛选
   public iDCardOpt: any[] = IDCardOpt;  //证件类型
   public cusCycleOpt: any[] = CusCycleOpt;	//生命周期
   public agricuOpt: any[] = AgricuOpt;//是否涉农
   public custAttr: any = [];//客户属性
   public isAgriculture: any = [];//涉农属性
   public creatStaDate: any;//起始日期
   public creatEndDate: any;//起始日期
   //客户属性
   public custAttrOpt: any = [];
   public custAttrOpt1: any[] = CustAttrOpt1;//（个人）
   public custAttrOpt2: any[] = CustAttrOpt2;//（企业）
   //信用评分
   public creditOpt: any[] = CreditOpt;//信用评分
   public creditStaOpt1: any[] = CreditStaOpt1;//信用评级起
   public creditEndOpt1: any[] = CreditEndOpt1;//信用评级止
   public creditStaOpt3: any[] = CreditStaOpt3;//贷后评级起
   public creditEndOpt3: any[] = CreditEndOpt3;//贷后评级止

   //5-1.个人客户属性筛选
   public sexOpt: any[] = SexOpt;//性别（下拉框）
   public marryOpt: any[] = MarryOpt;//婚姻状况（下拉框）
   public houseRegOpt: any[] = HouseRegOpt;//户口性质（下拉框）
   public birthDateFrom: any;//出生日期起
   public birthDateTo: any;//出生日期止

   //5-2.经营信息筛选
   public professOpt: any[] = ProfessOpt;//所属行业
   public enterTypeOPt: any[] = EnterTypeOPt;//中小型企业

   //6.信贷属性筛选

   //7.客户持有信息筛选
   public cusHoldProdOpt: any[] = CusHoldProdOpt;//客户持有产品标识

   //8.客户产品属性筛选
   public cusProAttrOpt: any[] = CusProAttrOpt;//客户持有产品标识
   public proSamllTypeOPt: any = [];//产品小类
   public paramOpt: any = [] = ParamOpt;//参数类型
   public saveTypeOpt: any[] = SaveTypeOpt;//客户小类（存款类）
   public loanTypeOpt: any[] = LoanTypeOpt;//客户小类（贷款类）
   public custHoldProdAttrList: any = [[]];
   public custHoldProdAttrListMember = {
       prodCateId: '',
       subProdCateId: '',
       paramTypeId: '',
       prodParmFrom: '',
       prodParmTo: ''
   }


   //9.客户标签属性筛选
   public cusTagOpt: any[] = CusTagOpt;//标签类型
   public cusTagNameOpt: any = []; //标签名称(模糊查询)
   public custLabelAttrList: any = [[]];// 客户标签属性列表
   public custLableAttrListMember = {
       labelTypeId: '',
       labelId: '',
       labelName: '',
       selectNameOpt: {}
   }


   //10.客户群属性筛选
   public CusGroupAttrOpt: any[] = CusGroupAttrOpt;//客户群类型
   // (模糊查询)
   public cusGroupNameOpt: any = [];//属性名称
   public groupName: any = [];
   public cusGroup: any = [];
   public custGroupAttrList: any = [[]];//客户群属性列表成员
   public custGroupAttrListMember = {
       custGrouTypeId: '',
       custGrouId: '',
       custGrouName: '',
       selectNameOpt: {}
   }


   //11.FTP利润属性筛选
   public fTPTimeOpt: any[] = FTPTimeOpt;//时间
   public fTPAttrOpt: any[] = FTPAtrrOpt;//属性
   public ftpAttrList: any = [[]];//FTP利润属性列表
   public ftpAttrListMember = {
       profit: '',
       profitAttr: '',
       profitParmFrom: '',
       profitParmTo: '',
   }
   cities;
   idCardNo;
   idCardType;
   constructor(
       public router: Router,
       public httpService: CusOperationHttpService,
       public commfunc: Commfunc,
       public route: ActivatedRoute,
   ) {
       let temp = this.copyObj(this.custHoldProdAttrListMember)
       this.custHoldProdAttrList = [[temp]] //初始化 一条值
       let temp1 = this.copyObj(this.custLableAttrListMember)
       this.custLabelAttrList = [[temp1]] //客户标签属性列表
       let temp2 = this.copyObj(this.custGroupAttrListMember)
       this.custGroupAttrList = [[temp2]]//客户群属性筛选
       let temp3 = this.copyObj(this.ftpAttrListMember)
       this.ftpAttrList = [[temp3]]//FTP利润属性

       /**
    * 接受目标客户探索管理页面(列表中发布为查询模板按钮)---（object-custom.component.ts）
        * 传过来的探索ID(custExplId)
    * 如果接受到值就查看详情
    */
       if (this.route.snapshot.params['custExplId']) {
           this.custExplId = this.route.snapshot.params['custExplId'];
           this.confirmClick();
       }
   }

   ngOnInit() {
       // 客户基本属性筛选
       this.creatCustExplCondBean.custInfo.custType = "1";// 客户属性默认个人
       this.custAttrOpt = this.custAttrOpt1;

   }

   //折叠面板的展开和收起
   listOpen() {
       this.trueOrFalse = true;
   }
   listClose() {
       this.trueOrFalse = false;
   }

   /**
  * 加减号的实现开始
  */
   // 对象拷贝
   copyObj(obj) {
       let temp = {};
       for (var i in obj) {
           temp[i] = obj[i]
       }
       return temp;
   }

   //点击或的加号(客户产品属性列表)
   doAdd(name) {
       //(客户产品属性列表)
       if (name == 'custHoldProdAttr') {
           let temp = this.copyObj(this.custHoldProdAttrListMember);
           this.custHoldProdAttrList.push([temp])
       }
       //客户标签属性
       if (name == 'custLabelAttr') {
           let temp = this.copyObj(this.custLableAttrListMember);
           this.custLabelAttrList.push([temp])
       }
       //客户群组属性
       if (name == 'custGroupAttr') {
           let temp = this.copyObj(this.custGroupAttrListMember);
           this.custGroupAttrList.push([temp])
       }
       //ftp
       if (name == 'ftpAttr') {
           let temp = this.copyObj(this.ftpAttrListMember);
           this.ftpAttrList.push([temp])
       }
   }

   //点击或的减号
   doReduce(i, name) {
       //(客户产品属性列表)
       if (name == 'custHoldProdAttr') {
           this.custHoldProdAttrList.splice(i, 1);
       }
       //(客户标签属性列表)
       if (name == 'custLabelAttr') {
           this.custLabelAttrList.splice(i, 1);
       }
       //(客户群组列表)
       if (name == 'custGroupAttr') {
           this.custGroupAttrList.splice(i, 1);
       }
       //ftp 利率
       if (name == 'ftpAttr') {
           this.ftpAttrList.splice(i, 1);
       }
   }

   //点击且中的加号(客户产品属性列表)
   doAndAdd(i, name) {
       //(客户产品属性列表)
       if (name == 'custHoldProdAttr') {
           let temp = this.copyObj(this.custHoldProdAttrListMember);
           this.custHoldProdAttrList[i].push(temp);
       }
       //(客户标签属性列表)
       if (name == 'custLabelAttr') {
           let temp = this.copyObj(this.custLableAttrListMember);
           this.custLabelAttrList[i].push(temp);
       }
       //(客户群组列表)
       if (name == 'custGroupAttr') {
           let temp = this.copyObj(this.custGroupAttrListMember);
           this.custGroupAttrList[i].push(temp);
       }
       //ftp 利率
       if (name == 'ftpAttr') {
           let temp = this.copyObj(this.ftpAttrListMember);
           this.ftpAttrList[i].push(temp);
       }
   }

   //点击且中的减号(客户产品属性列表)
   doAndReduce(ind, name) {
       //(客户产品属性列表)
       if (name == 'custHoldProdAttr') {
           if (this.custHoldProdAttrList[ind].length == 1) {
               this.doReduce(ind, 'custHoldProdAttr');
               return;
           }
           this.custHoldProdAttrList[ind].splice(this.custHoldProdAttrList[ind].length - 1, 1);
       }
       //(客户标签属性列表)
       if (name == 'custLabelAttr') {
           if (this.custLabelAttrList[ind].length == 1) {
               this.doReduce(ind, 'custLabelAttr');
               return;
           }
           this.custLabelAttrList[ind].splice(this.custLabelAttrList[ind].length - 1, 1);
       }
       //客户群组属性
       if (name == 'custGroupAttr') {
           if (this.custGroupAttrList[ind].length == 1) {
               this.doReduce(ind, 'custGroupAttr');
               return;
           }
           this.custGroupAttrList[ind].splice(this.custGroupAttrList[ind].length - 1, 1);
       }
       //ftp 属性
       if (name == 'ftpAttr') {
           if (this.ftpAttrList[ind].length == 1) {
               this.doReduce(ind, 'ftpAttr');
               return;
           }
           this.ftpAttrList[ind].splice(this.ftpAttrList[ind].length - 1, 1);
       }
   }
 /**
  * 加减号的操作结束
  */



   // 8.客户产品属性筛选
   //产品大类、产品小类、参数类型下拉框三级联动
   proChange(arry, event, i, ind) {
       if (event.value) {
           if (event.value == '10') {
               arry[i][ind]['proSamllTypeOPt'] = this.saveTypeOpt;
           } else if (event.value == '20') {
               arry[i][ind]['proSamllTypeOPt'] = this.loanTypeOpt;
           }
       } else {
           if (event == '10') {
               arry[i][ind]['proSamllTypeOPt'] = this.saveTypeOpt;
           } else if (event == '20') {
               arry[i][ind]['proSamllTypeOPt'] = this.loanTypeOpt;
           }
       }
   }
   subProChange(arry, event, i, ind, prodCateId) {
       if (prodCateId == '10') {
           this.saveTypeOpt.forEach(data => {
               if (event.value) {
                   if (event.value == data.value) {
                       arry[i][ind]['paramTypeOpt'] = this.paramOpt[0][data.value]
                   }
               } else {
                   if (event == data.value) {
                       arry[i][ind]['paramTypeOpt'] = this.paramOpt[0][data.value]
                   }
               }
           })
       } else if (prodCateId == '20') {
           this.loanTypeOpt.forEach(data => {
               if (event.value) {
                   if (event.value == data.value) {
                       arry[i][ind]['paramTypeOpt'] = this.paramOpt[0][data.value];
                   }
               } else {
                   if (event == data.value) {
                       arry[i][ind]['paramTypeOpt'] = this.paramOpt[0][data.value];
                   }
               }
           })
       }
   }

   
   /**
       * 模糊查询开始
       *
       */
   //客户标签名称（模糊查询）
   cusTagName(i, ind, event) {
       if (event.query) {
           //根据自己联想的标签名，进行模糊查询
           let param = { status: "1", labelName: event.query }
           this.selectLabelInfo(i, ind, param);
       } else {
           //根据回显详情后的标签名称（labelName），进行模糊查询
           let param = { status: "1", labelName: event }
           this.selectLabelInfo(i, ind, param);
       }
   }
   //调后台客户标签模糊查询接口
   selectLabelInfo(i, ind, param) {
       this.httpService.selectLabelInfo(param).subscribe(data => {
           if (data.returnCode.code == 'success') {
               this.custLabelAttrList[i][ind]['cusTagNameOpt'] = [[]];
               this.custLabelAttrList[i][ind]['cusTagNameOpt'] = data.labelTypeList;
               this.msgs = [];
               this.msgs.push({ severity: 'success', summary: '客户标签模糊查询', detail: data.returnCode.message });
           } else {
               this.msgs = [];
               this.msgs.push({ severity: 'error', summary: '客户标签模糊查询', detail: data.returnCode.message });
           }
       }, error => {
           this.msgs = [];
           this.msgs.push({ severity: 'error', summary: '客户标签模糊查询', detail: '调用服务失败！' });
       })
   }
   //客户标签模糊查询赋值，转化成后台想要的格式传给后台
   selectCusTagNameOpt(i, ind, event) {
       this.custLabelAttrList[i][ind]['labelId'] = event.labelNum;
       this.custLabelAttrList[i][ind]['labelName'] = event.labelName;
   }


   // 客户群属性（模糊查询）
   cusGroupName(i, ind, event) {
       if (event.query) {
           let param = { groupName: event.query, pageNum: 1, pageSize: 999999 };
           this.selectCustGroup(i, ind, param);
       } else {
           let param = { groupName: event, pageNum: 1, pageSize: 999999 };
           this.selectCustGroup(i, ind, param);
       }
   }
   selectCustGroup(i, ind, param) {
       this.httpService.selectCustGroup(param).subscribe(data => {
           if (data.returnCode.code == 'success') {
               this.custGroupAttrList[i][ind]['cusGroupNameOpt'] = [[]];
               this.custGroupAttrList[i][ind]['cusGroupNameOpt'] = data.groupList;
               this.msgs = [];
               this.msgs.push({ severity: 'success', summary: '客户群模糊查询', detail: data.returnCode.message });
           } else {
               this.msgs = [];
               this.msgs.push({ severity: 'error', summary: '客户群模糊查询', detail: data.returnCode.message });
           }
       }, error => {
           this.msgs = [];
           this.msgs.push({ severity: 'error', summary: '客户群模糊查询', detail: '调用服务失败！' });
       })
   }

   //客户属性模糊查询赋值,转化成后台想要的格式传给后台
   selectCusGroupNameOpt(i, ind, $event) {
       this.custGroupAttrList[i][ind]['custGrouId'] = $event.groupId;
       this.custGroupAttrList[i][ind]['custGrouName'] = $event.groupName;
   }
 /**
  * 模糊查询结束
  *
  */


   //客户基本属性筛选
   //客户属性下拉框
   radioBtn() {
       if (this.creatCustExplCondBean.custInfo.custType == '1') {
           this.custAttrOpt = this.custAttrOpt1;
       } else if (this.creatCustExplCondBean.custInfo.custType == '2') {
           this.custAttrOpt = this.custAttrOpt2;
       }
   }

   //确定按钮(探索条件详情回显)
   confirmClick() {
       let param = { "expCondId": this.custExplId };
       this.httpService.queryExploreConditionDetail(param).subscribe(data => {
           if (data.returnCode.code == 'success') {
               this.creatCustExplCondBean.sysModelName = data.exploreConditionName;
               // this.creatCustExplCondBean.statusId = data.statusId;//状态
               // this.creatCustExplCondBean.expCondId = data.expCondId;//探索条件ID         
               //4.客户基本属性筛选
               if (data.custInfo.creatTimeStart || data.custInfo.creatTimeEnd) {
                   this.creatStaDate = new Date(data.custInfo.creatTimeStart);//开始时间
                   this.creatEndDate = new Date(data.custInfo.creatTimeEnd);//结束时间
               }
               this.creatCustExplCondBean.custInfo = data.custInfo;
               this.radioBtn();
               // 5-1个人客户属性筛选
               if (data.indiCustomer) {
                   if (data.indiCustomer.birthDateStart || data.indiCustomer.birthDateEnd) {
                       this.birthDateFrom = new Date(data.indiCustomer.birthDateStart);//开始时间
                       this.birthDateTo = new Date(data.indiCustomer.birthDateEnd);//结束时间
                   }
                   this.creatCustExplCondBean.indiCustomer = data.indiCustomer;
               }
               // 5-2经营信息筛选
               if (data.businessInfo) {
                   this.creatCustExplCondBean.businessInfo = data.businessInfo;
               }
               //7.客户持有信息筛选
               if (data.custHoldProdLists) {
                   this.creatCustExplCondBean.custHoldProdLists = data.custHoldProdLists;
               }
               //8.客户产品属性筛选
               if (data.custHoldProdAttrList) {
                   data.custHoldProdAttrList.forEach((items, i) => {
                       items.forEach((item, ind) => {
                           this.custHoldProdAttrList = data.custHoldProdAttrList;
                           if (JSON.stringify(item) != "{}") {
                               this.proChange(this.custHoldProdAttrList, item.prodCateId, i, ind);
                               this.subProChange(this.custHoldProdAttrList, item.subProdCateId, i, ind, item.prodCateId);
                           }
                       })
                   })
               } else {
                   this.custHoldProdAttrList = [[]];
               }
               //9.客户标签属性筛选
               if (data.custLabelAttrList) {
                   this.custLabelAttrList = data.custLabelAttrList;
                   this.custLabelAttrList.forEach((items, i) => {
                       items.forEach((item, ind) => {
                           if (item.labelName) {
                               //将回显的值在进行一次模糊查询
                               this.cusTagName(i, ind, item.labelName);
                               //设置超时操作，防止异步操作造成的干扰
                               setTimeout(() => {
                                   if (this.custLabelAttrList[i][ind]['cusTagNameOpt']) {
                                       this.custLabelAttrList[i][ind]['cusTagNameOpt'].forEach(seleListItem => {
                                           //根据回显的客户标签名（labelName），去cusTagNameOpt数组里，去匹配对应数组进行ngModel绑定
                                           //（联想查询绑定的ngModel是个对象）
                                           if (item.labelName == seleListItem.labelName) {
                                               if (this.custLabelAttrList[i][ind]['selectNameOpt']) {
                                                   this.custLabelAttrList[i][ind]['selectNameOpt'] = seleListItem;
                                               } else {
                                                   this.custLabelAttrList[i][ind]['selectNameOpt'] = {};
                                                   this.custLabelAttrList[i][ind]['selectNameOpt'] = seleListItem;
                                               }
                                           }
                                       })
                                   }
                               }, 200)
                           }
                       })
                   })
               } else {
                   this.custLabelAttrList = [[]];
               }

               //10.客户群属性筛选
               if (data.custGroupAttrList) {
                   this.custGroupAttrList = data.custGroupAttrList;
                   this.custGroupAttrList.forEach((items, i) => {
                       items.forEach((item, ind) => {
                           if (item.custGrouName) {
                               //将回显的值在进行一次模糊查询
                               this.cusGroupName(i, ind, item.custGrouName);
                               //设置超时操作，防止异步操作造成的干扰
                               setTimeout(() => {
                                   if (this.custGroupAttrList[i][ind]['cusGroupNameOpt']) {
                                       this.custGroupAttrList[i][ind]['cusGroupNameOpt'].forEach(seleListItem => {
                                           //根据回显的客户群属性名（custGrouName），去cusGroupNameOpt数组里，去匹配对应数组进行ngModel绑定
                                           //注意：（联想查询绑定的ngModel是个对象）
                                           if (item.custGrouName == seleListItem.groupName) {
                                               if (this.custGroupAttrList[i][ind]['selectNameOpt']) {
                                                   this.custGroupAttrList[i][ind]['selectNameOpt'] = seleListItem;
                                               } else {
                                                   this.custGroupAttrList[i][ind]['selectNameOpt'] = {};
                                                   this.custGroupAttrList[i][ind]['selectNameOpt'] = seleListItem;
                                               }
                                           }
                                       })
                                   }
                               }, 200)
                           }
                       })
                   })
               } else {
                   this.custGroupAttrList = [[]];
               }

               //11.FTP利润属性筛选
               if (data.ftpAttrList) {
                   this.ftpAttrList = data.ftpAttrList;
               } else {
                   this.ftpAttrList = [[]];
               }
               this.msgs = [];
               this.msgs.push({ severity: 'success', summary: '探索条件详情', detail: data.returnCode.message });
           } else {
               this.msgs = [];
               this.msgs.push({ severity: 'error', summary: '探索条件详情', detail: data.returnCode.message });
           }
       }, error => {
           this.msgs = [];
           this.msgs.push({ severity: 'error', summary: '探索条件详情', detail: '调用服务失败！' });
       })
   }

   // 目标客户探索发布为模板
   PublishTemp() {

   // 4.客户基本属性筛选
   this.creatCustExplCondBean.custInfo.creatTimeStart = this.commfunc.transDateN(this.creatStaDate);//开始时间
   this.creatCustExplCondBean.custInfo.creatTimeEnd = this.commfunc.transDateN(this.creatEndDate);//结束时间
   //5-1.个人客户属性筛选
   if (this.birthDateFrom) {
     this.creatCustExplCondBean.indiCustomer.birthDateStart = this.commfunc.transDateN(this.birthDateFrom);
   }
   if (this.birthDateTo) {
     this.creatCustExplCondBean.indiCustomer.birthDateEnd = this.commfunc.transDateN(this.birthDateTo);
   }

 
   this.creatCustExplCondBean.custHoldProdAttrList = this.custHoldProdAttrList;//8.客户产品属性筛选
   this.creatCustExplCondBean.custLabelAttrList = this.custLabelAttrList;//9.客户标签属性筛选
   this.creatCustExplCondBean.custGroupAttrList = this.custGroupAttrList;//10.客户群属性筛选
       this.creatCustExplCondBean.ftpAttrList = this.ftpAttrList;//11.FTP利润属性筛选
   //深度克隆
   let cloneCreatCustExplCondBean = _.cloneDeep(this.creatCustExplCondBean);

   //8.客户产品属性筛选(将产品小类、参数类型从数组中删除)
   cloneCreatCustExplCondBean.custHoldProdAttrList.forEach(data => {
     data.forEach(item => {
       delete item.proSamllTypeOPt;
       delete item.paramTypeOpt;
     })
   })
   //9.客户标签属性筛选(将cusTagNameOpt从数组中删除)
   cloneCreatCustExplCondBean.custLabelAttrList.forEach(data => {
     data.forEach(item => {
       delete item.cusTagNameOpt;
       delete item.selectNameOpt;
     })
   })
   //10.客户群属性筛选(将cusTagNameOpt从数组中删除)
   cloneCreatCustExplCondBean.custGroupAttrList.forEach(data => {
     data.forEach(item => {
       delete item.cusGroupNameOpt;
       delete item.selectNameOpt;
     })
   })

       this.httpService.createScreenCustomerTemplate(this.creatCustExplCondBean).subscribe(data => {
           if (data.returnCode.code == 'success') {
               this.exploreConditionTempId = data.exploreConditionTempId;//生成探索模板ID
               this.msgs = [];
               this.msgs.push({ severity: 'success', summary: '客户探索模板', detail: data.returnCode.message });
           } else {
               this.msgs = [];
               this.msgs.push({ severity: 'error', summary: '客户探索模板', detail: data.returnCode.message });
           }
       }, error => {
           this.msgs = [];
           this.msgs.push({ severity: 'error', summary: '客户探索模板', detail: '调用服务失败！' });
       })
   }

   //保存按鈕
   saveClick() {
       this.creatCustExplCondBean.status = '2';//模板状态为创建成功
       this.PublishTemp();
   }

   //确定并提交按鈕
   confirmSaveClick() {
       this.creatCustExplCondBean.status = '3';//模板状态为有效
       this.PublishTemp();
       this.router.navigate(["pages/tzb/custom/marketing/new-object/object-custom"]);
   }


}
