import { Component, Output, EventEmitter, OnChanges, OnInit } from '@angular/core';
import { TempSharedRanOpt, TempStatusOpt, PublishTempShareRanOpt } from './../constant/target-custom.codeValue';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Message } from 'primeng/primeng';
import { CusOperationHttpService } from './../../../../http/custom-operation/custom-operation.http.service';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';
import { MatureTemplateBan } from "app/pages/tzb/custom/custom-information/advanced-search/add-search/open-search-template/bean/mature-template.bean";

@Component({
  selector: 'app-open-search-template',
  templateUrl: './open-search-template.component.html',
  styleUrls: ['./open-search-template.component.scss'],
  providers: [Commfunc, CusOperationHttpService]
})
export class OpenSearchTemplateComponent implements OnInit {
    
	// 表格中显示的信息
	tabMesg: any = tableMessage;

    /**
     * 选择后的值
     */
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    //bean
    matureTemplateBan: MatureTemplateBan = new MatureTemplateBan();
    //码值
    tempSharedRanOpt: any = TempSharedRanOpt;//共享范围（查询条件中的）
    // publishTempShareRanOpt: any = PublishTempShareRanOpt;//模板状态（列表中的）
    tempStatusOpt: any = TempStatusOpt;//模板状态（列表中的）
    //日期
    ch = CALENDAR_CN;//日期转化中文
    creatStartTime: any;//创建时间起
    creatEndTime: any;//创建时间起
    //单选选中(筛选模板ID)的值
    screenTemplateId: string = '';
    //总条数
    templToalNum: string = '';
    //表数据
    tempTableData: any;
    //提示信息
    msgs: Message[] = [];
    showFlag: boolean = false;
    orgTreeDisplay: boolean = false;
    orgTreeDisplay2: boolean = false;
    queryUserInValue: any;
    createOrgName: string;
    createUserName: string;
    status:string;
    constructor(
        public commfunc: Commfunc,
        public httpService: CusOperationHttpService
    ) { 

		this.creatEndTime = null;
		this.creatStartTime = null;
     }


    //选择模板按钮
    ngOnChanges() {
        // this.templQueryClick();
    }

    //选择模板按钮
    ngOnInit() {
        this.matureTemplateBan.requestType = "1";
        this.matureTemplateBan.pageSize = 10;
        this.matureTemplateBan.pageNum = 1;
        // this.templQueryClick();
    }

    //查询按钮
    templQueryClick() {
        if (this.createOrgName == '') {
            this.matureTemplateBan.createOrgId = null;
        }
        if (this.createUserName == '') {
            this.matureTemplateBan.createUserId = null;
        }
        this.matureTemplateBan.queryScene = "0";
        if (this.creatStartTime) {
            this.matureTemplateBan.creatTimeStart = this.commfunc.transDateN(this.creatStartTime);
        } else {
            this.matureTemplateBan.creatTimeStart = null;
        }
        if (this.creatEndTime) {
            this.matureTemplateBan.creatTimeEnd = this.commfunc.transDateN(this.creatEndTime);
        } else {
            this.matureTemplateBan.creatTimeEnd = null;
        }
        this.httpService.queryTargetCustomerExploreList(this.matureTemplateBan).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tempTableData = data.screenTemplateList;
                this.templToalNum = data.totalCount;
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '模板查询列表提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '模板查询列表提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '模板查询列表提示', detail: '调用服务失败！' });
        })
    }

    //确定按钮
    tempRconfirmClick() {
        this.outValue.emit({screenTemplateId:this.screenTemplateId,status:this.status});
    }

    //单选值
    checkRadioValue(event, item) {
        this.screenTemplateId = item.screenTemplateId;
        this.status = item.status;
    }
    //分页
    paginateTemp(event) {
        //每页显示的条数
        this.matureTemplateBan.pageSize = event.rows * 1;
        //当前页
        let currentPage = event.page + 1;
        this.matureTemplateBan.pageNum = currentPage;
        //调用查询的方法
        this.templQueryClick();
        this.matureTemplateBan.pageNum = 1;
    }
    //重置按钮
    tempResetClick() {
        this.matureTemplateBan = new MatureTemplateBan();
        this.createUserName = '';
        this.createOrgName = ''
        this.matureTemplateBan.requestType = "1";
        this.creatStartTime = null;
        this.creatEndTime = null;
    }

     // 浏览器滚动条状态 - 滚动
	onHideClose() {
		document.body.style.overflow = 'auto';
	  }


    // 主联系人机构
    orgFocus1() {
        this.orgTreeDisplay = !this.orgTreeDisplay;
        document.body.style.overflow = 'hidden';
    }
    // 主联系人
    userFocus1() {
        if(this.createOrgName){
            this.orgTreeDisplay2 = !this.orgTreeDisplay2;
        }else{
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请先选择机构' });
        }
        
        document.body.style.overflow = 'hidden';
    }
    // 机构树回调
    treeCall(param) {
        this.orgTreeDisplay = false;
        this.createOrgName = param.orgName;
        this.matureTemplateBan.createOrgId = param.orgId;
        this.queryUserInValue = param.orgId;
        document.body.style.overflow = 'auto';
    }
    perInfor(param) {
        this.matureTemplateBan.createUserId = [];
        this.orgTreeDisplay2 = false;
        this.createUserName = param[0].tellerName;
        this.matureTemplateBan.createUserId.push(param[0].tellerId);
        document.body.style.overflow = 'auto';
    }

    // 控制日历刷新
	refresh() {
		this.creatEndTime = null;
		this.showFlag = !this.showFlag;
	}
}
