
import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
//页面
import { AdvancedSearchComponent } from './advanced-search.component';
import { AddSearchComponent } from "./add-search/add-search.component";
import { AddSearchManageComponent } from "./add-search-manage/add-search-manage.component";
import { SearchConditionsManageComponent } from "./search-conditions-manage/search-conditions-manage.component";
import { PublishTemplateComponent } from "./search-conditions-manage/publish-template/publish-template.component";
import { SearchTemplateManageComponent } from "./search-template-manage/search-template-manage.component";
// import { TemplateDetailComponent } from "./search-template-manage/template-detail/template-detail.component";

const routes: Routes = [
    {
        path: '',
        component: AdvancedSearchComponent,
        children: [
            {path:'',redirectTo:'add-search',pathMatch:'full'},
            { path: 'add-search', component:AddSearchComponent},
            { path: 'add-search-manage', component: AddSearchManageComponent},
            { path: 'search-conditions-manage', component:SearchConditionsManageComponent},
            { path: 'publish-template', component:PublishTemplateComponent},
            { path: 'search-template-manage', component:SearchTemplateManageComponent},
            // { path: 'template-detail', component:TemplateDetailComponent},
            // { path: 'custom-performance', loadChildren:'./custom-performance/custom-performance.component.module#CustomPerformanceModule'}
        ]
    }
];
export const AdvancedSearchRouting: ModuleWithProviders = RouterModule.forChild(routes);
