import { Component, OnInit } from '@angular/core';
import { QueryTemplateBean } from "app/pages/tzb/custom/custom-information/advanced-search/query-template/bean/queryTemplate.bean";
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import { Message } from 'primeng/primeng';

@Component({
  selector: 'app-query-template',
  templateUrl: './query-template.component.html',
  styleUrls: ['./query-template.component.scss']
})
export class QueryTemplateComponent implements OnInit {
  public tmplList: any = [];
  public totalNum: number;
  public checkValue: any = [];
  public chooseData:any=[];
  //提示信息
  public msgs: Message[] = [];
  public queryTemplateBean: QueryTemplateBean = new QueryTemplateBean();
  constructor(public httpService: CusOperationHttpService, ) {
    this.tmplList = [
      { "tmplId": "001", "tmplName": "模板001" },
      { "tmplId": "002", "tmplName": "模板002" },
      { "tmplId": "003", "tmplName": "模板003" },
      { "tmplId": "004", "tmplName": "模板004" },
      { "tmplId": "005", "tmplName": "模板005" }
    ]
  }
  ngOnInit() {
    this.queryTemplateBean.pageSize = 10;
    this.queryTemplateBean.pageNum = 1;
  }
  search() {
    let ob = this.httpService.myCustAdvanceSearchTmplListQry(this.queryTemplateBean);
    ob.subscribe(data => {
      this.tmplList = data.tmplList;
      this.totalNum = data.totalNum;
      this.msgs = [];
      if (data.returnCode.code == 'success') {
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "删除失败" });
    })
  }
  //单选数据
  chooseTemplate(data){
    this.chooseData=data;
  }
  //确认
  confirm(){

  }
  //取消
  cancel(){
        this.checkValue = [];
  }
  //分页
  paginate(event) {
    this.queryTemplateBean.pageSize = event.rows * 1;
    let currentPage = event.page + 1;
    this.queryTemplateBean.pageNum = currentPage;
    this.search();
  }
}
